<template>
  <div class="content">
    <div class="personalMsg-content">
      <img class="personalMsg-content-touxiang" :src="personalMsg.avatarUrl" />
      <div class="personalMsg-content-detail">
        <div class="personalMsg-content-detail-title">
          <span class="personalMsg-content-detail-title-name">{{
            personalMsg.nickname
          }}</span>
        </div>
        <div class="personalMsg-content-detail-data">
          <div>
            <p>{{ personalMsg.eventCount }}</p>
            <p></p>
          </div>
          <div>
            <p>{{ personalMsg.follows }}</p>
            <p></p>
          </div>
          <div>
            <p>{{ personalMsg.followeds }}</p>
            <p></p>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="personalMsg-content-detail-title">
      <span class="personalMsg-content-detail-title-name"></span>
    </div>
    <playlists-component
      :playLists="createdPlayList"
      :playListShow="playListShow"
      idKey="id"
       playCountKey="playCount"
       imgKey="coverImgUrl"
       nameKey="name"
    ></playlists-component>
    <!--  -->
    <div class="personalMsg-content-detail-title">
      <span class="personalMsg-content-detail-title-name"></span>
    </div>
    <playlists-component
      :playLists="favoritePlayList"
      :playListShow="playListShow"
       idKey="id"
       playCountKey="playCount"
       imgKey="coverImgUrl"
       nameKey="name"
    ></playlists-component>
  </div>
</template>

<script>
import playlistsComponent from "../components/playilistsComponent";
export default {
  data() {
    return {
      userId: 0, //id
      personalMsg: {},
      createdPlayList: [], //
      favoritePlayList: [], //
      playListShow: false, //
    };
  },
  created() {},
  mounted() {
    this.userId = this.$route.query.userId;
    this.getPersonalMsg();
    this.getPlayList();
  },
  methods: {
    //
    getPersonalMsg() {
      this.g
        .axios({
          url: "/user/detail",
          params: {
            uid: this.userId,
          },
        })
        .then((res) => {
          console.log(res);
          this.personalMsg = res.data.profile;
        })
        .catch((err) => {
          console.error(err);
        });
    },
    // 
    getPlayList() {
      this.playListShow = false;
      this.g
        .axios({
          url: "/user/playlist",
          params: {
            uid: this.userId,
            limit: 10000, //,, 
          },
        })
        .then((res) => {
          console.log("", res);
          var playList = res.data.playlist;
          //
          this.favoritePlayList = playList.filter((item, i) => {
            if (item.userId == this.userId) {
              this.createdPlayList.push(item);
            }
            return item.userId != this.userId;
          });
          this.$nextTick(() => {
            var t = setTimeout(() => {
              this.playListShow = true;
              clearTimeout(t);
            });
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  watch: {
    $route(to, from) {
      //
      if (to.query.fm != from.query.fm) {
        this.getFMs();
      }
    },
  },
  components: {
    playlistsComponent,
  },
};
</script>

