import axios from 'axios'
import loadingComponent from './components/loadingComponent.vue'
export default {
    host: "http://49.232.0.103:3000",
    cookie: "",
    loadingAllPageHeight: 0, //loadingindex
    getUserId: function (userId) {
        this.userId = userId
    },
    userId: "",
    //,play,,,,
    getMusic: function (id, msg) {
        this.music.id = id;
        this.music.msg = msg
    },
    likedMusic: [], //
    music: {
        id: "",
        msg: {}
    },
    nextSong: {}, //
    prevSong: {}, //
    getNextSong: function (data) {
        this.nextSong = data;
    },
    getPrevSong: function (data) {
        this.prevSong = data;
    },
    getLikedMusic: function (data) {
        this.likedMusic = data;
    },
    axios: function (obj) {
        if (obj.url.indexOf("http://") == -1) {
            obj.url = this.host + obj.url
        }
        return new Promise((resolve, reject) => {
            if (!obj.params) obj.params = {};
            obj.params.cookie = this.cookie
            axios(obj).then(res => {
                resolve(res);
            }).catch(err => {
                reject(err)
            })
        })
    },
    //
    findPlayingMusic: function (list, id) {
        for (var i = 0; i <= list.length - 1; i++) {
            list[i].playing = false;
            if (id == list[i].id) {
                list[i].playing = true;
            }
        }
    },
    // loading
    install: function (Vue) {
        Vue.component('loadingComponent', loadingComponent);
    },
    print: function (title, msg) {
        console.log(title, JSON.parse(JSON.stringify(msg)))
    }
}