<template>
  <div
    class="content"
   
  >
  <!--  :style="{height: (loading ? `${contentHeight}px` : `auto`),overflow: loading ? `hidden` : `auto`}" -->
    <play-table
      :musicId="musicId"
      :songListRes="songListRes"
      :isGeDan="isGeDan"
      @songMsg="getSong"
      @songList="songList"
      @hideLoading="hideLoading"
    ></play-table>
    <loading-component ref="loading"></loading-component>
  </div>
</template>

<script>
import playTable from "../components/playTable.vue";
export default {
  props: ["musicId", "contentHeight"],
  data() {
    return {
      songListRes: [],
      albumId: "", //id
      isGeDan:true,///
    };
  },
  created() {},
  mounted() {
          this.isGeDan="zhuanji"
          this.getPlayRes();
       
  },
  methods: {
    //(id)
    getPlayRes() {
      this.albumId = this.$route.query.id;
      this.$refs.loading.show();
      this.g.axios({
        url: this.g.host + "/album",
        params: {
          id: this.albumId,
        },
      })
        .then((res) => {
          console.log("", res);
          //
          this.songListRes ={
            coverImgUrl:res.data.album.picUrl,//
            name:res.data.album.name,//
            description:res.data.album.description,//
            trackIds:res.data.songs
          };
        })
        .catch((err) => {
           this.$refs.loading.hide();
          console.log(err);
        });
    },
    //,index
    getSong(id, singer, zhuanji, index) {
      this.$emit("songMsg", id, singer, zhuanji, index);
    },
    songList(songList) {
      this.$emit("songList", songList);
    },
     hideLoading(){
      this.$refs.loading.hide();
    },
  },
  watch: {
    $route(to, from) {
      //
      if (to.query.id != from.query.id) {
        //id,
          this.getPlayRes();
      }
    },
  },
  components: {
    playTable,
  },
};
</script>

<style scoped>
.content {
  padding: 1.5rem;
  height: 100% !important;
  overflow: auto;
}
</style>