<script>
export default {
  /**
   * type://,
   * id:/id,
   * enableComment:
   */
  props: ["type", "id"],
  data() {
    return {
      commentSum: 0, //
      comments: [], //
      commentRepliedArr: [], //
      commentRepliedCount: 0, //
      page: 1, //
      repliedPage: 1, //
      pageSize: 30, // 
      similarSongs: [], //
      drawer: false, //
      commentLoadText: "", //, 
      repliedLoadText: "", //, 
      commentId: 0, //id,
    };
  },
  mounted() {
    var that = this;
    //,
    this.getDiscuss(this.id, 0, this.pageSize);
    // 
    this.getSimilarSongs(this.id);
    var commentLoadStatus = false; //,
    // ,,
    var t = "";
    this.$refs.compContent.addEventListener("scroll", function () {
      if (t) clearTimeout(t);
      t = setTimeout(() => {
        var scrollTopAndHeight = this.scrollTop + this.offsetHeight,
          scrollHeight = this.scrollHeight;
        //console.log(scrollTopAndHeight,scrollHeight - 20)
        // 20px, >-20,
        console.log(scrollTopAndHeight , scrollHeight - 20)
        if (scrollTopAndHeight > scrollHeight - 20 && !commentLoadStatus) {
          commentLoadStatus = true;
          that.page++;
          that.getDiscuss(
            that.id,
            (that.page - 1) * that.pageSize,
            that.pageSize,
            that.comments[that.comments.length - 1].time,
            () => {
              commentLoadStatus = false;
            }
          );
        }
      },100);
    });
  },
  methods: {
    //
    getDiscuss(id, offset, limit, before, success) {
      if (this.commentLoadText == "~") return;
      this.commentLoadText = "...";
      var url = "/comment/music";
      switch (this.type) {
        case "0": //
          url = "/comment/music";
          break;
        // case '1':
        //    url="/comment/music";
        //   break;
        case "2": //
          url = "/comment/playlist";
          break;
        case "3": //
          url = "/comment/album";
          break;
        default:
          break;
      }
      this.g
        .axios({
          url: url,
          params: {
            id: id,
            offset: offset, // , (  -1)*20
            limit: limit, //,100,20
            before: before, //time,100,
          },
        })
        .then((res) => {
          console.log("", res);
          var list = res.data.comments;
          //,, , ,
          if (offset == 0) {
            var hotList = res.data.hotComments; //
            this.commentSum = res.data.total;
            this.comments = hotList.concat(list); //
          } else {
            this.comments = this.comments.concat(list);
          }
          if (list.length < this.pageSize) {
            this.commentLoadText = "~";
          }
          this.$nextTick(() => {
            if (success) success();
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    getSimilarSongs(id) {
      this.g
        .axios({
          url: this.g.host + "/simi/song",
          params: {
            id: id,
          },
        })
        .then((res) => {
          console.log("", res);
          this.similarSongs = res.data.songs;
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // ,:id
    publishCommentOrLook(commentId, limit, time, success) {
      this.commentId = commentId;
      this.drawer = true;
      if (this.repliedLoadText == "~") return;
      this.repliedLoadText = "...";
      //
      this.g
        .axios({
          url: this.g.host + "/comment/floor",
          params: {
            parentCommentId: this.commentId, //id
            id: this.id, //id
            type: this.type, //
            limit: limit, //,100,20
            time: time, //time,100,
          },
        })
        .then((res) => {
          console.log("", res);
          this.commentRepliedCount = res.data.data.totalCount; //
          this.commentRepliedArr = this.commentRepliedArr.concat(
            res.data.data.comments
          );
          if (res.data.data.comments.length < this.pageSize) {
            this.repliedLoadText = "~";
          }
          this.$nextTick(() => {
            if (success) success();
          });
        })
        .catch((err) => {
          console.log(err);
        });
    },
    //
    drawerOpen() {
      var that = this;
      var commentRepliedLoadStatus = false; //,
      //this.$refs.commentRepliedContent.removeEventListener("scroll");
      // ,,
      this.$refs.commentRepliedContent.addEventListener("scroll", function () {
        var scrollTopAndHeight = this.scrollTop + this.offsetHeight,
          scrollHeight = this.scrollHeight;

        // 20px, >-20,
        if (
          scrollTopAndHeight > scrollHeight - 20 &&
          !commentRepliedLoadStatus
        ) {
          commentRepliedLoadStatus = true;
          that.repliedPage++;
          that.publishCommentOrLook(
            that.commentId,
            that.pageSize,
            that.commentRepliedArr[that.commentRepliedArr.length - 1].time,
            () => {
              commentRepliedLoadStatus = false;
            }
          );
        }
      });
    },
    //
    drawerClose() {
      this.commentRepliedArr = [];
      this.commentRepliedCount = 0;
      this.repliedPage = 1;
      this.repliedLoadText = "...";
    },
    // id,
    dianzan(commentId, liked, $event) {
      this.g
        .axios({
          url: this.g.host + "/comment/like",
          params: {
            id: this.g.music.id,
            cid: commentId, //id
            t: liked ? 0 : 1, //
            type: 0,
          },
        })
        .then((res) => {
          console.log("", res);
          //200
          if (res.data.code == 200) {
            //
            for (var i = 0; i <= this.comments.length - 1; i++) {
              if (this.comments[i].commentId == commentId) {
                //
                if (liked) {
                  this.comments[i].liked = false;
                  this.comments[i].likedCount--;
                } else {
                  this.comments[i].liked = true;
                  this.comments[i].likedCount++;
                }
                break;
              }
            }
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 
    linkToUserPage(userId) {
      this.$router.push({
        path: "/personal",
        query: {
          userId: userId,
        },
      });
    },
    //
    playSimilarSong(id, singer, zhuanji, index, name, pic) {
      var similarMsg = { name: name, pic: pic };
      this.$emit("similarSong", id, singer, zhuanji, index, similarMsg);
    },
    //
    timeToStr(time) {
      var date = new Date(time);
      var y = date.getFullYear();
      var m =
        date.getMonth() + 1 >= 10
          ? date.getMonth() + 1
          : "0" + (date.getMonth() + 1);
      var d = date.getDate();
      var h = date.getHours() >= 10 ? date.getHours() : "0" + date.getHours();
      var min =
        date.getMinutes() >= 10 ? date.getMinutes() : "0" + date.getMinutes();
      var s =
        date.getSeconds() >= 10 ? date.getSeconds() : "0" + date.getSeconds();
      return y + "" + m + "" + d + "" + " " + h + ":" + min + ":" + s;
    },
  },
  watch: {
    id() {
      //,
      this.commentLoadText = "...";
      this.getDiscuss(this.id, 0, this.pageSize);
      // 
      this.getSimilarSongs(this.id);
    },
  },
};
</script>

