<template>
  <div class="content">
    <div class="searchInputContent">
      <input
        type="text"
        placeholder=",,,..."
        v-model="searchVal"
      />
      <button @click="search"></button>
    </div>
    <el-tabs v-model="activeTab" @tab-click="handleClick">
      <el-tab-pane label="" name="song">
        <button @click="showMoreSong"></button>
        <play-table
          :songListRes="songListRes.songList"
          :musicId="musicId"
          :isGeDan="false"
          @songMsg="getSong"
          @songList="songList"
          @hideLoading="hideLoading"
        ></play-table>
      </el-tab-pane>
      <el-tab-pane label="" name="singer">
        <div class="singer-content">
          <div class="content-sum">{{ artist.artistCount }}</div>
          <div
            class="singer-content-item"
            v-for="(item, i) of artist.artistList"
            :key="i"
            :style="{
              backgroundColor: i % 2 == 0 ? 'white' : 'rgb(224,224,224)',
            }"
          >
            <img
              :src="item.picUrl ? item.picUrl : item.img1v1Url"
              alt=""
            />
            <span>{{ item.name }}</span>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="zhuanji">
        <div class="zhuanji-content">
          <div class="content-sum">{{ albums.albumsCount }}</div>
          <div
            class="zhuanji-content-item"
            v-for="(item, i) of albums.albumsList"
            :key="i"
            :style="{
              backgroundColor: i % 2 == 0 ? 'white' : 'rgb(224,224,224)',
            }"
            @click="showAlbum(item.id)"
          >
            <img
              :src="item.picUrl ? item.picUrl : item.img1v1Url"
              alt=""
            />
            <span>{{ item.name }}</span>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="gedan">
        <div class="gedan-content">
          <div class="content-sum">{{ gedan.gedanCount }}</div>
          <div
            class="gedan-content-item"
            v-for="(item, i) of gedan.gedanList"
            :key="i"
            :style="{
              backgroundColor: i % 2 == 0 ? 'white' : 'rgb(224,224,224)',
            }"
            @click="showGedan(item.id)"
          >
            <img :src="item.coverImgUrl" alt="" />
            <span>{{ item.name }}</span>
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="" name="video"></el-tab-pane>
    </el-tabs>
    <loading-component ref="loading"></loading-component>
  </div>
</template>

<script>
import playTable from "../components/playTable.vue";
export default {
  props: ["musicId"],
  data() {
    return {
      songListRes: { songCount: 0, songList: [], loadStatus: false }, //,songCount:,songList:,loadStatus:()
      artist: { artistCount: 0, artistList: [], loadStatus: false }, //
      albums: { albumsCount: 0, albumsList: [], loadStatus: false }, //
      gedan: { gedanCount: 0, gedanList: [], loadStatus: false }, //
      activeTab: "song",
      page: 1,
      searchVal: "", //
    };
  },
  created() {},
  mounted() {
    this.getSearchMusic();
  },
  methods: {
    getSearchMusic(activeTab, type) {
      var ajaxMsg = {
        url: this.g.host + "/search",
        params: {
          keywords: this.$route.query.searchVal,
          type: type,
          limit: 30,
          offset: (this.page - 1) * 30,
        },
      };

      switch (this.activeTab) {
        case "song": //
          
          if (!this.songListRes.loadStatus) {
            this.$refs.loading.show();
            this.g
              .axios(ajaxMsg)
              .then((res) => {
                console.log("", res);

                this.songListRes.loadStatus = true;
                this.songListRes.songList = this.songListRes.songList.concat(
                  res.data.result.songs
                );
              })
              .catch((err) => {
                console.log(err);
              });
          }
          break;
        case "singer": //
          this.g
            .axios(ajaxMsg)
            .then((res) => {
              console.log("", res);
              if (!this.artist.loadStatus) {
                this.artist.loadStatus = true;
                this.artist.artistCount = res.data.result.artistCount;
                this.artist.artistList = this.artist.artistList.concat(
                  res.data.result.artists
                );
              }
            })
            .catch((err) => {
              console.log(err);
            });
          break;
        case "zhuanji": //
          this.g
            .axios(ajaxMsg)
            .then((res) => {
              console.log("", res);
              if (!this.albums.loadStatus) {
                this.albums.loadStatus = true;
                this.albums.albumsCount = res.data.result.albumsCount;
                this.albums.albumsList = this.albums.albumsList.concat(
                  res.data.result.albums
                );
              }
            })
            .catch((err) => {
              console.log(err);
            });
          break;
        case "video": //
          console.log("", res);
          break;
        case "gedan": //
          this.g
            .axios(ajaxMsg)
            .then((res) => {
              console.log("", res);
              if (!this.gedan.loadStatus) {
                this.gedan.loadStatus = true;
                this.gedan.gedanCount = res.data.result.playlistCount;
                this.gedan.gedanList = this.gedan.gedanList.concat(
                  res.data.result.playlists
                );
              }
            })
            .catch((err) => {
              console.log(err);
            });
          break;
        default:
          break;
      }
    },
    //
    getSong(id, singer, zhuanji, index) {
      this.$emit("songMsg", id, singer, zhuanji, index);
    },
    songList(songList) {
      this.$emit("songList", songList);
    },
    //tabtab, e
    handleClick(tab, event) {
      //this.activeTabtab
      console.log(tab, event, this.activeTab);
      // 1  ,  : 1: , 10: , 100: , 1000: , 1002: , 1004: MV, 1006: , 1009: , 1014: , 1018:
      switch (this.activeTab) {
        case "song": //
          this.getSearchMusic(this.activeTab, 1);
          break;
        case "singer": //
          this.getSearchMusic(this.activeTab, 100);
          break;
        case "zhuanji": //
          this.getSearchMusic(this.activeTab, 10);
          break;
        case "video": //
          this.getSearchMusic(this.activeTab, 1014);
          break;
        case "gedan": //
          this.getSearchMusic(this.activeTab, 1000);
          break;
        default:
          break;
      }
    },
    //, 
    showAlbum(id) {
      this.$router.push({
        path: "/album",
        query: {
          id: id,
        },
      });
    },
    //, 
    showGedan(id) {
      this.$router.push({
        path: "/playlistDetail",
        query: {
          id: id,
        },
      });
    },
    //
    showMoreSong() {
      this.page++;
      this.getSearchMusic();
    },
    //
    search() {
      this.$router.push({
        path: "/search",
        query: {
          searchVal: this.searchVal,
        },
      });
    },
    hideLoading() {
      this.$refs.loading.hide();
    },
  },
  watch: {
    $route(to, from) {
      //
      if (to.query.searchVal != from.query.searchVal) {
        var searchVal = this.searchVal;
        // ,data
        Object.assign(this.$data, this.$options.data());
        this.searchVal = searchVal;
        this.getSearchMusic();
      }
    },
  },
  components: {
    playTable,
  },
};
</script>

