.content {
  padding: 1.5rem;
  height: 100%;
  overflow: auto;
}
/*  */
.searchInputContent {
  padding-bottom: 1rem;
  text-align: center;
}
.searchInputContent input {
  width: calc(100% - 2.3rem);
  height: 2.3rem;
  border-radius: 2rem 0 0 2rem;
  border: 1px solid #e3e3e3;
  border-right: 0;
  padding: 3px 5px;
  vertical-align: middle;
}
.searchInputContent button {
  width: 2.3rem;
  border-radius: 0 2rem 2rem 0;
  height: 2.3rem;
  border: 1px solid #e3e3e3;
  border-left: 0;
  background-color: white;
  background-image: url("../img/search.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 1.4rem 1.4rem;
  vertical-align: middle;
}
.content-sum {
  padding: 1rem;
  text-align: center;
}
/*  */
.singer-content-item,
.zhuanji-content-item,
.gedan-content-item {
  padding: 0.8rem;
}
.singer-content-item > span,
.singer-content-item > img,
.zhuanji-content-item > span,
.zhuanji-content-item > img,
.gedan-content-item > span,
.gedan-content-item > img {
  display: inline-block;
  vertical-align: middle;
}
.singer-content-item > img,
.zhuanji-content-item > img,
.gedan-content-item > img {
  width: 10rem;
